.nav-container {
    padding: 0px 30px 0px;
    width: 100%;
    height: 100%;
    min-height: 100px;
    /* max-width: 1240px; */
    /* display: flex; */
    display: grid;
    grid-template-columns: 180px 1fr 180px;
    grid-auto-flow: column;
    align-items: center;
    background: transparent;
    position: relative;
    z-index: 999;

}

.logo {
    justify-content: start;
}

.logo img {
    width: 5.5rem;
}

.navigation {
    justify-content: center;
    display: flex;
    text-align: center;
}

.navigation ul {
    display: flex;
    align-items: center;
    list-style-type: none;
}

.navigation li {
    text-transform: uppercase;
    padding: 0px 15px;
}

.nav-container .wallet {
    justify-content: end;
    text-align: end;
}

.wallet button {
    font-size: 0.7rem;
    font-family: 'Inter', sans-serif;
    font-weight: 700;
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    letter-spacing: 1px;
    padding: 20px 20px;
    background-color: #5E03C6;
    border-radius: 10px;
    border:none;
}

.wallet button img {
    width: 20px;
    margin-right: 10px;
}

.wallet .boom {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    color: #fff;
    /* position: relative; */
    justify-content: space-between;
    display: flex;
    align-items: center;
    /* margin: none; */
}

.wallet-button {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}



